@import './../../styles/mixins.scss';

$size: 150px;

.item {
  width: 385px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  transition: all 300ms;
  padding: 5px;

  @include onPhone {
    width: 320px;
    justify-content: center;
    align-items: center;
  }

  &__button {
    width: 150px;
    height: 150px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: transparent;
    border: none;
  }

  &__info {
    position: relative;
    width: 225px;
    height: 150px;
    padding: 6px;

    @include onPhone {
      width: 100%;
      height: auto;
    }
  }

  &__title {
    font-size: 24px;
    font-family: Poppins, sans-serif;
    font-weight: 700;
    text-align: end;

    @include onPhone {
      text-align: center;
    }
  }

  &__desc {
    display: block;
    font-family: Poppins, sans-serif;
    font-weight: 300;
    font-size: 15px;
    text-align: end;

    @include onPhone {
      width: 100%;
      display: block;
      text-align: justify;
    }
  }
  

  &__git {
    width: 30px;
    height: 30px;
  }

  &__demo {
    font-size: 15px;
    font-family: sans-serif;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: 10px;
    transition: all 300ms;
    cursor: pointer;

    &:hover {
      color: #fff;
    }
  }

  &__stack {
    width: $size;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3px;
    flex-wrap: wrap;

    @include onPhone {
      justify-content: start;
    }

    & * {
      width: 20px;
      height: 20px;
    }
  }
}