@import './styles/mixins.scss';

@font-face {
  font-family: ChrustyRock;
  src: url(./fonts/ChrustyRock-ORLA.ttf);
  font-style: normal;
  font-weight: 500;
}

@font-face {
  font-family: Hardick;
  src: url(./fonts/Newretrostyle3d-ygjm.ttf);
  font-style: normal;
  font-weight: 500;
}

@font-face {
  font-family: Poppins;
  src: url(./fonts//Poppins-Bold.ttf);
  font-style: normal;
  font-weight: 700;
}

@font-face {
  font-family: Poppins;
  src: url(./fonts//Poppins-Light.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: Roboto;
  src: url(./fonts/Roboto-Medium.ttf);
  font-style: normal;
  font-weight: 300;
}

.App {
  font-family: ChrustyRock, sans-serif;
  margin: 0 auto;
  max-width: 1600px;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
  padding: 100px 50px;

  @include onPhone {
    flex-direction: column;
    height: auto;
    padding: 0px 20px;
    width: 100%;
    margin-top: 80px;
    gap: 110px;
  }

  &__header {
    padding-top: 20px;
    position: absolute;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;

    & * {
      background-color: black;
      border-radius: 5px;
      width: 80px;
      height: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: all 300ms;
      cursor: pointer;
      
      &:hover {
        box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
        transform: scale(101%);
      }

      & * {
        width: 50px;
        height: 50px;
      }
    }
  }

  &__section {
    position: relative;

    &::after {
      content: "My-projects";
      position: absolute;
      top: -70px;
      left: -20px;
      color: aliceblue;
      font-size: 30px;
      transform: rotate(-12deg);
      z-index: 2;

      @include onPhone {
        top: -70px;
        left: 40%;
        transform: rotate(14deg);
      }
    
    }
  }



  &__field {

    width: 800px;
    height: 60vh;
    overflow-y: scroll;
    display: flex;
    justify-content: start;
    align-items: start;
    flex-wrap: wrap;
    gap: 30px;



    @include onPhone {
      height: auto;
      overflow-y: hidden;
      width: 100%;
      justify-content: center;
    }

    /* Для WebKit (Safari, Google Chrome і т.д.) */
    &::-webkit-scrollbar {
      width: 0.5em;
      display: none;
      /* ширина скролбару */
    }

    &::-webkit-scrollbar-track {
      background-color: transparent;
      /* колір фону трека */
      display: none;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #888;
      border-radius: 5px;
      /* колір скролбару */
      display: none;
    }

  }

  &__stack {
    display: flex;
    justify-content: center;

    & * {
      width: 50px;
      height: 50px;
    }
  }
}