@import './../../styles/mixins.scss';

@keyframes sliderShape{
  0%,100%{
  border-radius: 42% 58% 70% 30% / 45% 45% 55% 55%;
    transform: translate3d(0,0,0) rotateZ(0.01deg);
  }
  34%{
      border-radius: 70% 30% 46% 54% / 30% 29% 71% 70%;
    transform:  translate3d(0,5px,0) rotateZ(0.01deg);
  }
  50%{
    transform: translate3d(0,0,0) rotateZ(0.01deg);
  }
  67%{
    border-radius: 100% 60% 60% 100% / 100% 100% 60% 60% ;
    transform: translate3d(0,-3px,0) rotateZ(0.01deg);
  }
}

.about {
  position: relative;
  width: 400px;
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  gap: 10px;

  @include onPhone {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 25px;
    padding: 0 15px;
  }

  &__photo {
    width: 250px;
    height: 250px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border: none;
    border-radius: 100%;
    box-shadow: rgb(71, 71, 244) 0px 0px 0px 2px inset, rgba(255, 255, 255, 0) 10px 10px 0px -3px, rgb(31, 193, 27) -10px -10px, rgba(255, 255, 255, 0) 20px 20px 0px -3px, rgb(255, 217, 19) -20px -20px, rgba(255, 255, 255, 0) 30px 30px 0px -3px, rgb(255, 156, 85) -30px -30px, rgba(255, 255, 255, 0) 40px 40px 0px -3px, rgb(255, 85, 85) -40px -40px;

    @include onPhone {
      margin-bottom: 15px;
      border-radius: 62% 47% 82% 35% / 45% 45% 80% 66%;
      will-change: border-radius, transform, opacity;
      animation: sliderShape 5s linear infinite;
      display: block;
      z-index: -1;
      -webkit-animation: sliderShape 10s linear infinite;
    }
  }

  

  &__info {
    width: 375px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    padding: 10px;

    @include onPhone {
      width: 100%;
    }
  }

  &__name {
    font-family: Poppins;
    display: block;
    width: 100%;
    font-size: 60px;
    font-weight: 900;
    margin-bottom: 10px;

    @include onPhone {
      font-size: 50px;
      text-align: center;
    }
  }


  &__desc {
    font-family: Poppins, sans-serif;
    font-weight: 500;

    & * {
      margin-left: 5px;
      width: 20px;
      height: 20px;
    }

    @include onPhone {
      display: block;
      width: 100%;
      text-align: center;
    }
  }

  &__stack {
    height: 250px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: end;
    flex-wrap: wrap;
    gap: 3px;

    @include onPhone {
      width: 100%;
      flex-direction: row;
      justify-content: center;
      align-items: start;
      height: auto;
      margin-bottom: 10px;
    }

    & * {
      width: 35px;
      height: 35px;
    }
  }
}